











import { Vue, Component } from 'vue-property-decorator';

@Component({
    components: {},
})
export default class VuePageClass extends Vue {
    //#region  Variables
    private logsHtml: string = '';
    //#endregion

    //#region Vue life
    private async beforeCreate() {}
    private async created() {
        this.initLogs();
    }
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}
    //#endregion

    //#region Init Data
    private async initLogs(): Promise<void> {
        const publicPath: string = __webpack_public_path__ || '/./';
        const url: string = `${window.location.origin}${publicPath}change.log`;

        try {
            const { data } = await this.axios.get(url);
            this.logsHtml = this.resolveLogs(data);
        } catch (e) {
            console.error(e);
        }
    }
    //#endregion

    //#region Resolve data
    private resolveLogs(file): string {
        let log: string = file.toString();
        let logs: string[] = log.replace(/\r/g, '').split(/\n/);

        let logVersonDirectory: object = {};
        for (let i: number = 0, key: string = ''; i < logs.length; i++) {
            let log: string = logs[i];

            let match: RegExpMatchArray = log.match(/message: v[0-9].[0-9]{2}.[0-9]{2}$/);
            if (!!match && match.length > 0) {
                key = match[0].replace(/message: /, '');
                logVersonDirectory[key] = '';
                continue;
            }

            if (!key) continue;

            logVersonDirectory[key] += `<li>${log}</li>`;
        }

        let html: string = '';
        Object.keys(logVersonDirectory).forEach((value, index, array) => {
            let key = value;
            let list = logVersonDirectory[value];

            html += `
                    <div>
                        <h2 style='margin:0px'>${key}</h2>
                        <ul style='margin-top:0.5rem'>${list}<ul>
                    </div>`;
        });
        return html;
    }
    //#endregion
}
